@import '../../styles/variables';
@import '../../styles/media';

.label {
  display: flex;
  flex-direction: column;
  position: relative;
}

.labelTitle {
  display: block;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 500;
  padding-left: 10px;
  color: $c-Isabelline;

}

.textarea {
  outline: none;
  border: 1px solid rgba($c-Antique-White, 0.1);
  cursor: pointer;
  border-radius: 15px;
  background-color: $c-Antique-White;
  transition: $t-normal;
  margin-bottom: 30px;
  padding: 10px;
  height: 55px;
  width: 100%;
  &:hover {
    box-shadow: 0px 4px 10px 0px rgba($c-Black-Coral, 0.5);
  }
  &:focus {
    box-shadow: 0px 4px 10px 0px rgba($c-Black-Coral, 0.5);
  }
}

.errorMessage {
  position: absolute;
  color: #d82a2a;
  font-size: 18px;
  bottom: 5px;
  right: 0px;
}
